   }
   
   
   .register-link a {
    color: #007F86;
    text-decoration: none;
   }
   
   
   .form-group {
    margin-bottom: 15px;
   }
   
   
   .form-group label {
    display: block;
    font-weight: bold;
   }
   
   
   
   
