   /* Datepicker Container */
  .datepicker-container {
      z-index: 9999; /* Make sure it's above other elements */
      background-color: white;
      border: 1px solid #ddd;
      border-radius: 8px;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
      padding: 10px;
      width: auto;
      max-width: 350px;
      margin-top: 10px;
    }
    
    /* Styling the calendar header (Month and Year) */
    .day-picker-header {
      font-weight: bold;
      font-size: 16px;
      text-align: center;
      color: #444;
      margin-bottom: 10px;
    }
    
    /* Styling the Day buttons */
    .day {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: transparent;
      color: #333;
      font-size: 14px;
      cursor: pointer;
      transition: background-color 0.3s ease, color 0.3s ease;
    }
    
    .day:hover {
      background-color: #f0f0f0;
      color: #333;
    }
    
    /* Selected day */
    .day.selected {
      background-color: #007BFF;
      color: white;
    }
    
    /* Disabled days */
    .day.disabled {
      background-color: #ddd;
      color: #999;
      cursor: not-allowed;
    }
    
    /* Today's day */
    .day.today {
      background-color: #FFD700;
      color: black;
    }
    
    /* Hover styles for day (when selecting multiple) */
    .day:hover:not(.disabled) {
      background-color: #e0e0e0;
      color: #000;
    }
    
    /* Styling for the arrows (previous/next month navigation) */
    .DayPicker-NavButton {
      background-color: #007BFF;
      color: white;
      border-radius: 50%;
      border: none;
      padding: 5px;
      cursor: pointer;
      font-size: 18px;
      transition: background-color 0.2s ease;
    }
    
    .DayPicker-NavButton:hover {
      background-color: #0056b3;
    }
    
    /* Styling for the input fields within search */
    .search-container input[type="text"] {
      padding: 10px;
      font-size: 16px;
      border: 1px solid #ccc;
      border-radius: 4px;
      margin-right: 10px;
      width: 300px;
    }
    
    /* Styling search field containers */
    .search-field input {
        width: 100%;  /* Subtract the border width */
        padding: 10px;
        font-size: 14px;
        box-sizing: border-box;
        border: 1px solid #ccc;
        border-radius: 4px;
        position: relative; /* Maintain relative positioning for dropdown */
        z-index: 10;
    }
    
      
    /* Styling search labels */
    .search-field label {
      display: block;
      margin-bottom: 8px;
      font-weight: bold;
      color: #666;
    }
    
   
   
   
   
   
   
   
   
   